@tailwind base;
@tailwind components;
@tailwind utilities;
/* Personalizaciones globales */
body {
  @apply bg-gray-100 text-gray-800 font-sans antialiased;
}
h1 {
  @apply text-3xl font-bold mb-4;
}
h2 {
  @apply text-2xl font-semibold mb-2;
}
p {
  @apply text-base leading-relaxed;
}
input, button, select {
  @apply rounded-lg border px-3 py-2;
}
button {
  @apply transition-colors duration-200 ease-in-out hover:bg-opacity-80;
}
.container {
  @apply max-w-6xl mx-auto px-4;
}
.card {
  @apply bg-white rounded-xl shadow p-4;
}
.alert {
  @apply bg-blue-50 border border-blue-200 text-blue-800 px-4 py-2 rounded-lg mb-4 shadow-sm;
}

